<template>
    <div>
        <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>我的客户</el-breadcrumb-item>
          <el-breadcrumb-item>客户详情</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区 -->
        <el-card class="box-card">
          <!-- 卡片头部 -->
            <div slot="header" class="clearfix">
              <span>客户详情</span>
              <!-- <el-button style="float: right; padding: 3px 0" type="text" icon="el-icon-d-arrow-left" @click="returnHandle">返回</el-button> -->
            </div>
        <!-- 卡片内容 表单区 -->
            <el-form :model="editDetailRuleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
               <el-form-item label="客户状态" prop="customerStatusId">
                <el-select v-model="editDetailRuleForm.customerStatusId" placeholder="请选择客户状态">
                  <el-option label="跟进中" value="1"></el-option>
                  <el-option label="已成交" value="2"></el-option>
                  <el-option label="已失效" value="3"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="客户名称"  prop="customerName">
                <el-input v-model="editDetailRuleForm.customerName" disabled class="el-input-width"></el-input>
              </el-form-item>
              <el-form-item label="联系人" prop="contactPersonName">
                <el-input v-model="editDetailRuleForm.contactPersonName" class="el-input-width"></el-input>
              </el-form-item>
               <el-form-item label="客户电话" prop="contactPersonPhone">
                <el-input v-model="editDetailRuleForm.contactPersonPhone" class="el-input-width"></el-input>
              </el-form-item>
               <el-form-item label="客户微信" prop="contactPersonWechat">
                <el-input v-model="editDetailRuleForm.contactPersonWechat" class="el-input-width"></el-input>
              </el-form-item>
               <el-form-item label="阿里旺旺号" prop="contactPersonWangwang">
                <el-input v-model="editDetailRuleForm.contactPersonWangwang" class="el-input-width"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="editDetailSubmitForm('ruleForm')" plain>提交修改</el-button>
                <el-button @click="editDetailResetForm('ruleForm')" plain type="danger">重置</el-button>
              </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>
<script>
// import Qs from 'qs'
export default {
  data () {
    return {
      // 新增表单信息数组
      editDetailRuleForm: {
        customerStatusId: '',
        customerName: '',
        contactPersonName: '',
        contactPersonPhone: '',
        contactPersonWechat: '',
        contactPersonWangwang: ''
      },
      rules: {
        customerStatusId: [
          { required: true, message: '请选择客户状态', trigger: 'change' }
        ]
        // customerName: [
        //   { required: true, message: '请填写客户名称', trigger: 'blur' }
        // ]
      }
    }
  },
  created () {
    // console.log(this.$route.params.id)
    // this.getCustomerDetail()
  },
  methods: {
    async getCustomerDetail () {
      // const data = Qs.stringify({ customerId: this.$route.params.id })
      // const { data: res } = await this.$http.post('/customer/findCustomerById', data)
      // console.log(res)
    },
    editDetailSubmitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let flag = this.addCustomRuleForm.CustomerPhone.trim() || this.addCustomRuleForm.CustomerWechat.trim() || this.addCustomRuleForm.CustomerWangwang.trim()
          if (!flag) {
            return this.$message.error('至少输入一种联系方式！')
          }
          // 验证手机号的规则
          const regMobile = /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/
          if (regMobile.test(this.addCustomRuleForm.CustomerPhone.trim())) {
            // 调用新增客户接口

            return this.$message.success('提交成功！未写')
          } else {
            return this.$message.error('请输入合法得手机号！')
          }
        } else {
          return this.$message.error('提交失败！')
        }
      })
    },
    editDetailResetForm (formName) {
      this.$refs[formName].resetFields()
    },
    // 点击返回按钮触发该事件，返回上一页
    returnHandle () {
      this.editDetailResetForm('ruleForm')
      this.$router.push('/detail/customerDetail')
    }
  }
}
</script>
<style lang="less" scoped>
// 表单输入框宽度
@el-input-width:500px;
.el-input-width{
    width: @el-input-width!important;
}
</style>
