<template>
    <div>
         <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item :to="{path:'/my/CustomerPublicArea'}">客户公海</el-breadcrumb-item>
          <el-breadcrumb-item>公海详情</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card class="box-card">
          <!-- 卡片头部 -->
            <div slot="header" class="clearfix">
              <span>公海详情</span>
              <el-button style="float: right; padding: 3px 0" type="text" icon="el-icon-d-arrow-left" @click="returnHandle">返回</el-button>
            </div>
          <!-- 卡片内容区 -->
           <div class="cardContent">
                <el-row v-if="currentUserType!==2" >
                  <el-button  style="float: right;" type="primary" plain size="large" @click="removeOtherHandle">转入</el-button>
                  <el-select v-model="userValue" placeholder="请选择" style="float: right;" @change="selectChange" >
                    <el-option  v-for="item in userList"  :key="item.userId"  :label="item.userName"   :value="item.userId">   </el-option>
                  </el-select>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">客户名称</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight textRightColor">{{customerPublicAreaMsg.customerName}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">客户状态</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight" >
                        <el-tag type="danger" v-if="customerPublicAreaMsg.customerStatusId==='1'">跟进中</el-tag>
                        <el-tag type="success" v-else-if="customerPublicAreaMsg.customerStatusId==='2'">已成交</el-tag>
                        <el-tag type="info" v-else>已失效</el-tag>
                      </span>
                  </el-col>
                </el-row>
                 <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">上次跟进时间</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight">{{customerPublicAreaMsg.contactPersonUpdatedDate}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">联系人</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight">
                          {{customerPublicAreaMsg.contactPersonName}}
                      </span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">联系电话</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight">{{customerPublicAreaMsg.contactPersonPhone}}</span>
                  </el-col>
                </el-row>
                 <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">微信</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight">{{customerPublicAreaMsg.contactPersonWechat}}</span>
                  </el-col>
                </el-row>
                 <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">阿里旺旺号</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight">{{customerPublicAreaMsg.contactPersonWangwang}}</span>
                  </el-col>
                </el-row>

                <!-- <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">下次提醒时间</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight textRightColor"></span>
                  </el-col>
                </el-row>
                 <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">下次跟进主题</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight"></span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">下次跟进内容</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight" ></span>
                  </el-col>
                </el-row> -->
            </div>
        </el-card>
    </div>
</template>
<script>
import Qs from 'qs'
export default {
  data () {
    return {
      customerPublicAreaMsg: {},
      currentUserType: null,
      userList: [],
      userValue: null,
      userName: ''
    }
  },
  created () {
    this.currentUserType = window.sessionStorage.getItem('userType') - 0
    this.customerPublicAreaMsg = this.$route.query
    this.getUserList()
    this.getCustomerAreaDetail()
  },
  methods: {
    // 点击返回触发该事件，返回上一页
    returnHandle () {
      this.$router.push({ path: '/my/CustomerPublicArea' })
    },
    // 点击转入按钮触发该事件
    async removeOtherHandle () {
      // customerPublicAreaStatus
      const data = Qs.stringify({ customerId: this.$route.query.id, userId: this.userValue, customerPublicAreaStatus: 1, userName: this.userName })
      const { data: res } = await this.$http.post('/customer/updateCustomer', data)
      if (res.status !== 200) return this.$message.error('修改用户信息失败')
      this.$router.push({ path: '/my/CustomerPublicArea' })
      this.$message.success(res.msg)
    },
    async getUserList () {
      const { data: res } = await this.$http.post('/user/findUser')
      const arr1 = res.data.items
      let arr = []
      arr = arr1.forEach(item => {
        if (item.userType === 2) {
          arr.push(item)
        }
        this.userList = arr
      })
    },
    async getCustomerAreaDetail () {
      const data = Qs.stringify({ customerId: this.$route.query.id })
      const { data: res } = await this.$http.post('/customer/findCustomerById', data)
      this.customerPublicAreaMsg = {
        customerName: res.data.customer.customerName || '',
        customerStatusId: res.data.customer.customerStatusId || '',
        contactPersonName: res.data.contactPersonList[0].contactPersonName || '',
        contactPersonPhone: res.data.contactPersonList[0].contactPersonPhone || '',
        contactPersonWechat: res.data.contactPersonList[0].contactPersonPhone || '',
        contactPersonWangwang: res.data.contactPersonList[0].contactPersonWangwang || '',
        contactPersonUpdatedDate: res.data.contactPersonList[0].contactPersonUpdatedDate || ''
      }
    },
    async selectChange (e) {
      if (e) {
        const data = Qs.stringify({ userId: e })
        const { data: res } = await this.$http.post('/user/findUser', data)
        this.userName = res.data.userName
      }
    }

  }

}
</script>
<style lang="less" scoped>
// 左侧文本颜色
@textLeftColor: #949393;
// 右侧文本着重变色
@textRightColor: #fdb42d;
.textLeft{
    position: relative;
    top:0 ;
    left: 330px;
    font-size: 16px;
    color: @textLeftColor;
}
.textRight{
    position: relative;
    top: 0;
    right: 100px;
    font-size: 14px;
}
.textRightColor{
    color: @textRightColor;
}
.el-row {
    margin-bottom: 20px;
}
</style>
